			style="margin: 0 44rpx 0 49rpx; height: 76rpx; line-height: 76rpx; color: #ee5b30;">
				CONFIRM
			</view>
			<view class="" style="height: 53rpx;"></view>
		</view>
		<!-- My Devices -->
		<view class="" v-if="myDevicesFlag">
			<view class="" style="height: 80rpx;"></view>
			<view class="df-jcc-aic" >
				<view class="bgc_FFF df-jcc-aic" @click="addDevice"
				hover-class="hover" hover-start-time=0 hover-stay-time=150 
				style="border: 6rpx solid #ee5b30; border-radius: 26rpx; flex-direction: column; width: 278rpx; height: 190rpx;">
					<view class="">
						<image src="../../static/MyACCout/Add.png" mode="widthFix" style="width: 100rpx;"></image>
					</view>
					<view class="f25rpx fw800" style="color: #ee5b30;">Add Device</view>
				</view>
			</view>
			<view class="f30rpx ta-c fw800" style="color: #ee5b30; margin: 46rpx 0 51rpx 0;">
				CONNECTED THE TEKYGO! PORTAL</view>
			<view class="bgc-o-r b-r-15 df" style="margin: 0 68rpx 68rpx; height: 160rpx; align-items: center;">
				<view class="" style="margin: 0 36rpx;">
					<image src="../../static/img/Tekygoicon.png" mode="widthFix" style="width: 120rpx;"></image>
				</view> 
				<view v-if="ifBind" class="FFF f30rpx fw800">SN {{this.serial}}</view>
				<view v-if="!ifBind" class="FFF f30rpx fw800">NOT CONNECTED</view>
			</view>
			<view v-show="ifBind" class="df-jcc-aic" >
				<view @click="unBind" class="bgc_FFF df-jcc-aic" 
				hover-class="hover" hover-start-time=0 hover-stay-time=150 
				style="border: 6rpx solid #ee5b30; border-radius: 26rpx; flex-direction: column; width: 278rpx; height: 190rpx;">
					<view class="">
						<image src="../../static/MyACCout/Delete.png" mode="widthFix" style="width: 100rpx;"></image>
					</view>
					<view class="f25rpx fw800" style="color: #ee5b30;">Unbind</view>
				</view>
			</view>
		</view>
		<!-- myGames -->
		<view class="" v-if="myGamesFlag">
			<view class="" v-show="myGamesDateFlag">
				<view class="" style="height: 60rpx;"></view>
				<view class="f36rpx FFF fw800 ta-c" style="margin-bottom: 160rpx;">MY GAMES</view>
				<view class="" style="margin: 0 80rpx;" >
					<scroll-view scroll-y="true" style=" height: calc(100vh - 420rpx);">
						<view v-for="(item,index) in myGeatData" :key='index' 
						style="margin-bottom: 11rpx;" class="kaPian  b-r-30 bgc_FFF">					
							<view class="df-jcsb b-r-30" @click="gomyGeatData(item)" 
							hover-class="hover_FFF" hover-start-time=0 hover-stay-time=150
							style="height: 100%; align-items: center;">
								<view class="" style="width: 180rpx;">
									<view class="df-jcc-aic ta-c"> 
										<image :src="item.iconUrl" 
										style="width: 150rpx; height: 130rpx;"></image> 
									</view>
								</view> 
								<view class="ta-c" style=""> 
									<view class="f24rpx fw700" style="color: #ec5d31;">{{item.title}}</view>
								</view> 
								<view  class="df" style="width: 140rpx; align-items: center;">
									<view class="df-jcc-aic b-r-50 box-sha" style=" height: 71rpx; width: 71rpx;">
										<image class="" src="../../static/Index/sjx.png" mode="widthFix" 
										style="width: 30rpx;"></image>
									</view>
								</view>
							</view>
						</view>
					</scroll-view>
				</view>
			</view>
			<view class="" style="" v-if="gamesDetailFlag">
				<view class="" style="height: 50rpx; width: 100%;"></view>
				<gamesDetail :dates="dates" :day="days"></gamesDetail>
			</view>
			<view class="" @click="gamesBack" v-if="gamesDetailFlag"
				hover-class="hover1" hover-start-time=0 hover-stay-time=150
				style="position: absolute; top: 150rpx; left: 50rpx;">
				<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
			</view>
			
		</view>
		<!-- mySubscription -->
		<view class="" v-show="mySubscriptionFlag">
			<view class="" style="height: 55rpx;"></view>
			<view class="f36rpx FFF fw800 ta-c" style="">MY SUBSCRIPTION</view>
			<view v-if="!active" class="bgc-o-r b-b-r-l-52 " style="margin: 0 85rpx;">
				<view class="df-jcc-aic" style="padding-top: 170rpx;">
					<image src="../../static/mlestones/MH.png" mode="widthFix" 
					style="width: 98rpx; height: 98rpx; background-color: #fdc84f;" class="b-r-50"></image>
				</view> 
				<view class="f30rpx FFF ta-c" style="margin: 66rpx 20rpx 91rpx;">
					<view class="" style="">We noticed that your subscription is </view>
					<view class="">
						<text style="color: #fecb4d; padding-right: .5em;">no longer valid </text>  
						<text>or has expired.</text> 
					</view> 
					<view class="">Please purchase subscription again! </view>
				</view>
				<view class="bgc-o-y b-r-15 ta-c fw800" @click="goPurchase"
				hover-class="hoverDAA" hover-start-time=0 hover-stay-time=150
				style="margin: 0 48rpx 0 45rpx; height: 76rpx; line-height: 76rpx; color: #ee5b30;">
					PURCHASE
				</view>
				<view class="" style="height: 89rpx;"></view>
			</view>
			<view v-if="active" class="bgc-o-r b-b-r-l-52 " style="margin: 0 85rpx;">
				<view class="df-jcc-aic" style="padding-top: 170rpx;">
					<image src="../../static/MyACCout/Layer6842.png" mode="widthFix" class="b-r-50"
					style="width: 98rpx; height: 98rpx; background-color: #fdc84f;"></image>
				</view>
				<view class="FFF fw800 f30rpx ta-c" style="margin-top: 65rpx;">
					<view class="">
						<text>State: </text>
						<text style="color: #fecb4d; margin-left: 10rpx;">Activated</text>
					</view>
					<view class="">
						Thank you for your support !
					</view> 
				</view>
				<view class="" style="height: 305rpx;"></view>
			</view>
		</view>
		
	<view class="showmodel" v-show="ifGo">
		<view class="" style="height: 30vh;"></view>
		<view class="bgc_FFF" 
		style="margin: 0 78rpx; border-radius: 51rpx; height: 500rpx; border: 1rpx solid #9ec0d7;">
			<view class="df-jcc-aic" style="">
				<image  src="../../static/fristPage/Head3.png" mode="widthFix" 
				style="width: 166rpx; margin-top: 30rpx;"></image>
			</view>  
			<view class="ta-c color-H0f649c f33rpx" style="margin: 20rpx 20rpx 20rpx; ">
				{{content}}
			</view>
			<view class="FFF f33rpx ta-c bgc-o-r fw800" @click="goLogin"
			style="line-height: 84rpx; margin: 0 42rpx; height: 84rpx; border-radius: 42rpx; ">
				{{key}} 
			</view>
		</view>
	</view>
	</view>
</template>  

<script>
	import gamesDetail from '../index/gameDetails.vue'
	  export default {
		components:{gamesDetail},
	    data() {
	      return {
					email:'',
					bgcFlag:true,
	        myAccountFlag:true,
					ProfileFlag:false,
					myDevicesFlag:false,
					myGamesFlag:false,
					mySubscriptionFlag:false,
					ifBind:false,
					ifCountry:false,
					myGamesDateFlag:true,
					gamesDetailFlag:false,
					ifGo:false,
					windowHeight:false,
					day:'',
					days:'',
					data:16,
					url:'',
					id:'',
					name:'',
					phone:'',
					city:'',
					serial:'',
					country:'',
					Tram_id:'',
					myGeatData:'',
					dates:'',
					ifVideo:true,
					h:4,
					flags:1,
					num:0,
					active:false,
					content:'We see that you are not subscribed! Please subscribe to access this feature and many more!',
					key:'Login',
					sbId:'',
					
	      }
	    },
	    onLoad(){
	      uni.showLoading({
	      	title:'Loading...'
	      })
	      let time2 = setTimeout(function() {
	      	uni.hideLoading()
	      	console.log('1');
	      	// clearTimeout(time)   
	      }, 500);   
	    },
			updated() {
				this.getBindState()
			},
			created() {
				let that = this
				uni.getSystemInfo({
					success(res) {
						if(res.windowHeight >700){
							that.windowHeight = true
						}else{
							that.windowHeight = false
						}
					}
				})
			},
			mounted() {
				this.getBindState()
				// this.bindSyntony()
				this.getUserDate()
				this.getList()
			},
			updated() {
				// this.getUserDate()
			},
			onShow() {
				this.getBindState()

				let _this = this
				uni.getNetworkType({
						success: function (res) {
								console.log(res.networkType);
								if(res.networkType == 'none'){
									// console.log('无网络');
									_this.flags = 2
								}else{
									// console.log('有网络');
									_this.flags = 1
								}
						}
				});
				this.$nextTick(function(){
					if(this.flags == 2){
						this.$refs.myAcc.openPopup('Request out of time, please check network and try it again')
					}else{
						
					}
				})

			},
			onHide(){
				this.gamesDetailFlag = false
				this.myGamesDateFlag = true
			},
			onTabItemTap(){
				let isLogin = uni.getStorageSync('isLogin')
				if(!isLogin){
					this.ifGo = true
				}else{
					this.ifGo = false

				} 
			},
	    methods: {
				goLogin(){
					uni.reLaunch({
						url:'../welcomePage/inputEmail'
					})
				},
				getBindState(){
					this.email = uni.getStorageSync('Email')
				},
				
			  async switchConfien(){
					console.log('调用一次');
					if(this.name == ''){
						this.$refs.myAcc.openPopup('Enter Name')
						return false
					}
					if(this.phone == ''){
						this.$refs.myAcc.openPopup('Enter the phone')
						return false
					}
					if(this.city == ''){
						this.$refs.myAcc.openPopup('Select the city')
						return false
					}
					this.$refs.MyAccLoad1.openLoad()
					const res = await this.$myRequest({
						url:'/api/user/update',
						method:"POST",
						data:{
							"nickname":this.name,
							"phone":this.phone,
							"countryCode":this.city
						}
					})
					console.log(res);
					if(res.data.succeeded){
						this.$refs.MyAccLoad1.closeLoad()
						this.$refs.myAcc.openPopup('Modify Success')
					}else{
						this.$refs.MyAccLoad1.closeLoad()
						this.$refs.myAcc.openPopup(res.data.errmsg)
					}
				},
				
				clickImage(){
					this.ifVideo = false
				},
				videoStop(){
					console.log('暂停了');
					this.ifVideo = true
				},
				swiperFun(e){
					this.num = e.detail.current
					// console.log(e);
					// console.log(this.num);
				},

				//获取游戏列表
				async getList(){
					var _this = this
					const res = await this.$myRequest({
						url:'/api/category/list',
					})
					let id = res.data.data.records[0].id
					const res1 = await this.$myRequest({ 
						url:'/api/game/list'+'?categoryId='+id+'&page=1'+'&limit=-1',
					}) 
					if(res1.data.succeeded){
						this.myGeatData = res1.data.data.records
					}
				 
				}, 
				gomyGeatData(e){
					console.log(e);
					this.dates = e
					this.day = e.createTime
					console.log('day='+this.day);
					let year = this.day.slice(0,4)
					let month = this.day.slice(5,7)
					let day = this.day.slice(8,10)
					this.days = day + '/'+month+'/'+year
					this.bgcFlag = false
					this.myGamesDateFlag = false
					this.gamesDetailFlag = true
				},
				gamesBack(){
					this.bgcFlag = true
					this.myGamesDateFlag = true
					this.gamesDetailFlag = false
				},
				
				cilckCanada(){
					this.country = 'Canada'
					this.city = 'CA'
					if(this.ifCountry){
						this.ifCountry = false
					}
				},
				clickUSA(){
					this.country = 'America/USA'
					this.city = 'US'
					if(this.ifCountry){
						this.ifCountry = false
					}
				},
				ifConsent(){
					console.log('点击了');
					this.IfConsent = !this.IfConsent
				},
				clickCounty(){
					console.log('点击了');
					this.ifCountry = !this.ifCountry
				},
				myAccountBack(){
					this.bgcFlag = true
					this.myAccountFlag = true
					this.ProfileFlag = false
					this.myDevicesFlag = false
					this.myGamesFlag = false
					this.mySubscriptionFlag = false
				},

				goProfile(){
					this.bgcFlag = false
					this.myAccountFlag = false
					this.myDevicesFlag = false
					this.myGamesFlag = false
					this.mySubscriptionFlag = false
					this.ProfileFlag = true
				},
				myReport(){
					uni.switchTab({ 
						url:'../milestones/milestones'
					})
				},
				myDevices(){
					this.bindSyntony()
					this.bgcFlag = true
					this.myAccountFlag = false
					this.ProfileFlag = false
					this.myGamesFlag = false
					this.mySubscriptionFlag = false
					this.myDevicesFlag = true
				},
				myGames(){
					this.bgcFlag = true
					this.myAccountFlag = false
					this.myDevicesFlag = false
					this.ProfileFlag = false
					this.mySubscriptionFlag = false
					this.myGamesFlag = true
				},
			 
				async mySubscription(){
					this.$refs.MyAccLoad1.openLoad()
					const res = await this.$myRequest({
						url:'/api/sub/get'
					})
					const res2 = await this.$myRequest({
						url:'/api/sub/validate'
					})
					console.log(res);
					console.log(res.data.data); 
					console.log(res2);
					if(res.data.succeeded && res2.data.succeeded){
						this.$refs.MyAccLoad1.closeLoad()
						this.url = res.data.data 
						this.active = res2.data.data.active
						console.log('this.active'+this.active);
						console.log(this.active);
						
						this.bgcFlag = false
						this.myAccountFlag = false
						this.myDevicesFlag = false
						this.ProfileFlag = false
						this.myGamesFlag = false
						this.mySubscriptionFlag = true
					}else if(res.data.succeeded==false && res2.data.succeeded){
						this.$refs.myAcc.openPopup(res.data.errmsg)
					}else if(res.data.succeeded && res2.data.succeeded==false){
						this.$refs.myAcc.openPopup(res2.data.errmsg)
					}
				}, 
				
				goPurchase(){
					uni.redirectTo({
						url:'../subscription/subscription'
					})
					// plus.runtime.openURL(this.url); 
					// this.mySubscriptionFlag = false
					// this.myAccountFlag = true
					// this.bgcFlag = true
				}, 
				
				async addDevice(){
					var that = this
					uni.scanCode({
						onlyFromCamera: true,                   
						success(res) {                                              
							that.id = res.result
							uni.setStorageSync("id",that.id)
							// console.log(this.id);
							that.scanCodes()
						}
					
					})
				},
				//扫码绑定设备
				async scanCodes(){
					// var that = this
					const res = await this.$myRequest({
						url:'/api/device/bind'+'?processId='+ this.id,
					}) 
					console.log(res); 
					if(res.data.succeeded){
						console.log('绑定成功');
						this.ifBind = true 
						uni.setStorageSync("isBind",this.isBind)
						this.$refs.myAcc.openPopup('Binding success!')
						this.bindSyntony()
					}else{
						console.log('绑定失败');
						uni.setStorageSync("isBind",false)
						this.$refs.myAcc.openPopup(res.data.errmsg)
					}
				}, 
				
				async bindSyntony(){
					const res = await this.$myRequest({
						url:'/api/device/list' + '?page=1' +'&limit=-1',
					})
					console.log('展示');
					console.log(res)
					// console.log(res.data.data.records[0].serial);
					if(res.data.data.records == ''){
						this.ifBind = false
					}else{
						this.ifBind = true
						this.serial = res.data.data.records[0].serial
						console.log('serial='+this.serial);
						this.sbId = res.data.data.records[0].id
					}
				},  
				
				async unBind(){
					console.log('1');
					// let id = uni.getStorageSync('id')
					// console.log(id);
					let _this = this
					const res = await this.$myRequest({
						url:'/api/device/unbind',
						method:'POST',
						data:{
							"id":_this.sbId
						} 
						
					})
					// console.log(this.id);
					// console.log(res);
					if(res.data.succeeded){
						this.ifBind = false
						// uni.removeStorageSync('id')  
						// uni.removeStorageSync('serial')
						// uni.setStorageSync("ifBind",false)
						this.$refs.myAcc.openPopup('You have successfully unconnected your App from your Portal!')
					}else{
						this.$refs.myAcc.openPopup("There is no bound device!")
					}
				},
				
				//获取用户信息
				async getUserDate(){
					const res = await this.$myRequest({
						url:'/api/user/get',
					})
					if(res.data.succeeded){
						this.name = res.data.data.firstName
						this.phone = res.data.data.lastName
						this.city = res.data.data.countryCode
					} 
					if(this.city == 'CA'){
						this.country = 'Canada'
					}else{
						this.country = 'America/USA'
					}

				} 
			
			}
	  }

		 	

</script>

